@import url(https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,300;0,500;0,700;1,300;1,500;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --font-size-smaller: 14px;
  --font-size-standard: 16px;
  --font-size-bigger: 18px;
  --border-radius: 5px;
  --border-color: rgb(204, 204, 204);
  --light-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  --heavy-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  --light-grey: rgba(238, 238, 238, 0.5);
  --light-grey-no-opacity: rgb(238, 238, 238);
}

* {
  font-family: 'Prompt', sans-serif;
  font-size: 16px;
  font-size: var(--font-size-standard);
  box-sizing: border-box;
}

.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 10px 5% 2%;
  min-height: 320px;
  min-width: 250px;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.loader-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader-container > p {
  -webkit-animation: pulse 0.5s infinite alternate;
  animation: pulse 0.5s infinite alternate;
}

.welcome-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background: white;
  min-height: 320px;
  min-width: 220px;
}

.google-btn {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 16px 0 0;
  background-color: #4285f4;
  border: solid 2px rgb(204, 204, 204);
  border: solid 2px var(--border-color);
  border-radius: 5px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  transition: 0.25s;
}

.google-btn:hover {
  box-shadow: 1px 4px 5px 1px #4285f4;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  cursor: pointer;
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  height: 40px;
  width: 40px;
}

.google-icon {
  transition: 0.25s;
}

.google-btn:hover img {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.btn-text {
  margin: 0 8px;
  color: white;
}

.privacy-policy {
  position: absolute;
  bottom: 10px;
}

.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 5px;
  border-radius: var(--border-radius);
  outline: none;
  border: 1px solid #ccc;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

#search-bar-container {
  position: -webkit-fixed;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 0 5%;
  z-index: 1;
  background: rgba(255, 255, 255, 0.95);
}

#search-bar {
  display: flex;
  flex-direction: row;
  padding: 20px 0;
  border-bottom: 1px solid rgb(204, 204, 204);
  border-bottom: 1px solid var(--border-color);
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  box-shadow: var(--light-shadow);
}

.city-search {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  position: relative;
  margin-right: 10px;
}

.city-search__input {
  width: 100%;
}

.city-search__suggestions {
  visibility: hidden;
  opacity: 0;
  width: 100%;
  max-height: 60vh;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 5px 0 0;
  top: 43px;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  box-shadow: var(--heavy-shadow);
  overflow: scroll;
  background-color: rgb(238, 238, 238);
  background-color: var(--light-grey-no-opacity);
  border-radius: 5px;
  border-radius: var(--border-radius);
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--border-color);
  transition: visibility 0.15s, opacity 0.15s linear;
}

.city-search__suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  cursor: default;
  transition-duration: 0.2s;
}

.city-search__suggestions > li:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-left-radius: var(--border-radius);
  border-bottom-right-radius: 5px;
  border-bottom-right-radius: var(--border-radius);
}

.city-search__suggestions > li:hover {
  background-color: #bcdcdb;
}

.city-search__suggestions > li:first-child:hover {
  background-color: unset;
}

.city-search__button {
  background: none;
  border: none;
  width: 100%;
  text-align: left;
  padding: 0;
  margin: 5px 0;
}

.city-search__button:hover {
  cursor: pointer;
}

.city-search__all-button {
  background: none;
  border: none;
  border-top: 1px solid rgb(204, 204, 204);
  border-top: 1px solid var(--border-color);
  width: 100%;
  text-align: center;
  margin-top: 0;
  padding-top: 8px;
}

.city-search__all-button:hover {
  cursor: pointer;
}

.city-search__error {
  position: absolute;
  top: 50px;
  max-width: 30em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  background-color: #fff;
  padding: 5px 10px;
  font-size: 1.25em;
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  box-shadow: var(--heavy-shadow);
}

.city-search__error::before {
  content: '';
  position: absolute;
  width: 0;
  height: 0;
  bottom: 100%;
  left: 1.5em;
  border: 0.75rem solid transparent;
  border-top: none;
  border-bottom-color: #fff;
  -webkit-filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, 0.1));
  filter: drop-shadow(0 -0.0625rem 0.0625rem rgba(0, 0, 0, 0.1));
}

.number-of-events {
  display: flex;
  justify-content: flex-end;
  align-content: flex-start;
  font-size: 14px;
  font-size: var(--font-size-smaller);
  width: 50px;
  height: 38px;
}

.number-of-events__input {
  text-align: center;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
}

#content {
  margin-top: 89px;
}

#offline-alert {
  display: flex;
  justify-content: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: red;
  color: #fff;
  z-index: 1;
}

#chart-wrapper {
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: -15px;
  grid-gap: 30px;
}

#genre-chart-wrapper {
  height: 70vh;
  margin-top: 15px;
}

#city-chart-wrapper {
  overflow-x: scroll;
  overflow-y: hidden;
  height: 70vh;
  margin-top: 15px;
}

.recharts-cartesian-axis-tick:hover {
  cursor: pointer;
}

#clear-button {
  position: absolute;
  top: 70vh;
  right: 5%;
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.1);
  box-shadow: var(--heavy-shadow);
}

#chart-controls {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

@media all and (max-width: 500px) {
  #genre-chart-wrapper {
    height: 30vh;
  }

  #city-chart-wrapper {
    height: 30vh;
  }

  #clear-button {
    top: 65vh;
  }

  #chart-controls {
    justify-content: space-evenly;
  }

  #chart-wrapper {
    display: flex;
    flex-direction: column;
  }
}

#hide-chart {
  margin: 0 5px 0 2vw;
}

.event-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.event-list__item {
  width: 100%;
}

.event-list__item:first-of-type > div {
  margin: max(20px, 2vh) 0 2vh;
}

.event-list__warning {
  width: 100%;
  margin-top: calc(2vh - 4px);
}

.show-more-events {
  margin-top: 2vh;
  max-width: 100px;
}

.show-more-events:hover {
  cursor: pointer;
}

.show-more-events > p {
  transition: 0.3s;
}

.show-more-events:hover > p {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.show-more-events:hover > span {
  -webkit-animation: bounce;
  animation: bounce 0.5s infinite alternate;

  /* margin-top: 3vh; */
}

.event {
  padding: 10px;
  min-width: 250px;
  margin: 2vh 0;
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--border-color);
  border-radius: 5px;
  border-radius: var(--border-radius);
  position: relative;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
  box-shadow: var(--light-shadow);
  background-color: rgba(3, 112, 139, 0.075);
  transition-duration: 0.3s;
}

.event:hover,
.event:focus {
  -webkit-transform: scale(1.005);
          transform: scale(1.005);
}

.event__window {
  text-align: left;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  outline: none;
  cursor: unset;
}

.event__show-more-button {
  width: 100%;
  padding: 10px 0 0;
  text-align: center;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  transition-duration: 0.01s;
}

.turn {
  -webkit-transform: rotate(-0.5turn);
          transform: rotate(-0.5turn);
}

.event__summary {
  font-size: 18px;
  font-size: var(--font-size-bigger);
  font-weight: 600;
}

.event__details {
  text-align: justify;
  font-weight: 300;
  background-color: white;
  border-radius: 5px;
  border-radius: var(--border-radius);
  padding: 5px 10px;
}

.group-name {
  text-transform: uppercase;
}

.going,
.visibility {
  font-style: italic;
}

.event__description {
  overflow-x: hidden;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.recharts-surface {
  overflow: visible;
}

.display-none {
  display: none !important;
}

@media all and (max-height: 400px) {
  .privacy-policy {
    margin-top: 20px;
    position: unset;
  }
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }

  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }
}

@-webkit-keyframes bounce {
  from {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 60%, 0);
            transform: translate3d(0, 60%, 0);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 60%, 0);
            transform: translate3d(0, 60%, 0);
  }
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  to {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: none;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


